import { Component } from '@angular/core';
import { NgForm } from '@angular/forms';

import { IonicPage, NavController, AlertController } from 'ionic-angular';

import { UserService } from '../../providers/user-service';

@IonicPage()
@Component({
	selector: 'page-user',
	templateUrl: 'signup.html'
})
export class SignupPage {
	signup: {email?: string, password?: string} = {};
	submitted = false;

	constructor(public navCtrl: NavController, public UserService: UserService, public alertCtrl: AlertController) {}

	onSignup(form: NgForm) {
		this.submitted = true;

		if (form.valid) {
			this.UserService.signup(this.signup).then(data => {
				this.navCtrl.pop();
				this.alert("Sucesso", "Registo concluído com sucesso.");
			}, error => {
				if(error.status == 409) {
					this.alert("Erro", "Este email já se encontra registado.");
				}
				else {
					this.alert("Erro", "Ocorreu um erro.");
				}
			});
		}
	}

	alert(title: string, subtitle?: string) {
		let alert = this.alertCtrl.create({
			title: title,
			subTitle: subtitle,
			buttons: ['Ok']
		});
		alert.present();
	}
}



